<script setup>
import { ref, onMounted } from "vue";

const props = defineProps({
  lang: {
    type: String,
    default: "kz",
  },
});

const images = ref([]);

const fileNames = [
  "modo1.jpeg",
  "modo2.jpeg",
  "modo3.jpeg",
];

async function loadImages() {
  const loaded = [];

  for (const file of fileNames) {
    try {
      const mod = await import(
        `@/assets/images/pages/methodImages/modo/${file}`
      );
      loaded.push(mod.default || mod);
    } catch (e) {
      console.error(`Не удалось загрузить изображение: ${file}`, e);
    }
  }

  images.value = loaded;
}

onMounted(loadImages);
</script>

<template>
  <div class="backgroundFrame">
    <div class="page pageFour">
      <h1>
        {{ lang === "kz" ? "ООЖБ дейінгі уақыт бұрын" : "Задолго до МОДО" }}
      </h1>
      <p>
        {{
          lang === "kz"
            ? `Оқушылардың оқу жетістіктерін бақылау (ООЖБ) білім беру ұйымдарынан тәуелсіз тиісті білім беру деңгейінің мемлекеттік жалпыға міндетті білім беру стандартына сәйкестігін тексеру мақсатында, оқу сапасын жүйелі түрде бақылау болып табылады. 8-сынып оқушыларына арналған пәндердің спецификацияларына сәйкес, бірінші оқу тоқсанынан кейін осы іс-шараға дайындықты сынақ тестілеулері түрінде ұйымдастыру, оқыту үдерісіндегі олқылықтарды жою және ең күрделі тақырыптарды игеру үшін аралық нәтижелер мониторингінің процедураларымен және талаптарымен танысу мақсатында жүргізілді. Мұғалімдердің жүйелі және жоспарлы жұмысы тестілеу кезінде жұмыс уақытын дұрыс ұйымдастыруға көмектеседі, тапсырмаларды орындауда сенімділікті арттырады. Сынақ тестілеуі 8-сыныптарда ақпараттық-коммуникациялық технологияларды қолдану арқылы кешенді тестілеу түрінде оқу тілінің үш функционалдық сауаттылық бағыты бойынша жүргізілді: оқу сауаттылығы, математикалық сауаттылық, жаратылыстану ғылымдары сауаттылығы. Сынақ тестілеуін талдау нәтижелері бойынша мұғалімдер каникул және жазғы мектеп кезеңінде жұмыс істей алатын «ақсаған» тақырыптарды анықтау мүмкіндігіне ие болады.`
            : `Мониторинг образовательных достижений обучающихся (МОДО) является независимым от организаций образования систематическим наблюдением за качеством обучения на соответствие государственному общеобязательному стандарту соответствующего уровня образования. В целях знакомства с процедурой и требованиями, мониторинга промежуточных результатов для устранения пробелов и отработки наиболее сложных тем по спецификациям предметов согласно планирования  после первой учебной четверти организована подготовка к данному мероприятию в форме пробных тестирований  учащихся 8  классов. Системная и планомерная работа педагогов поможет правильно организовать рабочее время в момент тестирования, повысит уверенность в выполнении заданий. Пробное тестирование в 8-х классах проводилось в форме комплексного тестирования с применением информационно-коммуникационных технологий на языке обучения по трем направлениям функциональной грамотности: грамотность чтения, математическая грамотность, естественнонаучная грамотность. По результатам анализа пробного тестирования у педагогов будет возможность выявить «западающие» темы, которые можно будет отработать во время каникулярной и летней школы.`
        }}
      </p>
      <div class="gallery">
        <img
          v-for="(src, index) in images"
          :key="index"
          :src="src"
          alt="methodImage"
          class="gallery-image"
        />
      </div>
    </div>
    <div class="bottomP"></div>
  </div>
</template>
